

const Nav = ( {className, prefix, routes, onNavClick}) => {

    


    const handleClick= (e) => {
        
        onNavClick(e)

    }

    return (
        <nav className={className}>

            <ul>
                {Object.keys(routes).map((route, i) => (
                    
                    <li key={i}>
                        {prefix ? 

                        <a value={route} onClick={e => handleClick(e)} href={`#/${prefix}/${route}`}><span>{routes[route]}</span></a> :
                        <a value={route} onClick={e => handleClick(e)} href={`#/${route}`}><span>{routes[route]}</span></a> }
                         
                        
                    </li>
                ))}

            </ul>
            
        </nav>
    )
}

export default Nav
