
import '../MxNet/MxNetLayout.css'
import { useState, useEffect } from 'react'

import MxiLayoutOptions from '../MXI/MxiLayoutOptions'
import MxiWorkscopeLayout from '../MXI/MxiWorkscopeLayout'


const OnBaseLayout = ({updateOptions, selectedOption}) => {
    const [activeLayout, setActiveLayout] = useState('workscope')


    const updateActiveLayout = (layout) => {
        console.log('setting active layout..')
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        
        console.log('e.target.value: ', e.target.value)
        setActiveLayout(e.target.value)

    }

    return (
        <>

        
        {/* return the options to app to update the state */}
        {activeLayout === 'workscope' ? updateOptions(MxiLayoutOptions) : updateOptions(null)}
        {}
        
        
        <div className='mxnet-layout-container'>
            <div className='layout-options-container'>
                <button value={'workscope'} onClick={e => onLayoutButtonClick(e)}>Workscope</button>
                
                   
            </div>
            {activeLayout === 'workscope' ? <MxiWorkscopeLayout selectedOption={selectedOption} /> : <MxiWorkscopeLayout />}



        </div>

        </>
    )
}

export default OnBaseLayout
