import image from '../../resources/images/RTS Details Page.PNG'
const RtsDetailsLayout = () => {
    return (
        <div className = 'image-container'>
            <svg viewBox="0 0 fit-content fit-content">
                <image href={image} width="100%"  />
                
                
                <a id="date-range" href=""><rect x="445" y="110" width="910" height="50" /></a>
                <a id="search-statuses" href=""><rect x="445" y="172" width="910" height="70" /></a>
                <a id="other-options" href=""><rect x="445" y="255" width="910" height="180" /></a>
            
            </svg>  
        </div>
    )
}

export default RtsDetailsLayout


