
import './MxNetLayout.css'
import { useState, useEffect } from 'react'
// import InfoPanel from '../InfoPanel'
import SearchOptions from './SearchOptions'
import RtsDetailsOptions from './RtsDetailsOptions'
import SearchLayout from './SearchLayout'
import RtsDetailsLayout from './RtsDetailsLayout'


const MxNetLayout = ({updateOptions, selectedOption}) => {
    const [activeLayout, setActiveLayout] = useState('SearchLayout')


    const updateActiveLayout = (layout) => {
        console.log('setting active layout..')
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        
        console.log('e.target.value: ', e.target.value)
        setActiveLayout(e.target.value)

    }

    return (
        <>

        
        {/* return the options to app to update the state */}
        {activeLayout === 'SearchLayout' ? updateOptions(SearchOptions) : updateOptions(RtsDetailsOptions)}
        {}
        
        
        <div className='mxnet-layout-container'>
            <div className='layout-options-container'>
                <button value={'SearchLayout'} onClick={e => onLayoutButtonClick(e)}>Search Page</button>
                <button value={'RTS Details Page'} onClick={e => onLayoutButtonClick(e)}>RTS Details Page</button>
                   
            </div>
            {activeLayout === 'SearchLayout' ? <SearchLayout selectedOption={selectedOption} />
             : <RtsDetailsLayout selectedOption={selectedOption}/>}
            {/* <MainLayout /> */}
            {/* <RtsDetailsLayout /> */}


        </div>

        </>
    )
}

export default MxNetLayout
