
import '../MxNet/MxNetLayout.css'
import { useState, useEffect } from 'react'

import OnBaseWorkflowOptions from '../OnBase/OnBaseWorkflowOptions'
import OnBaseWorkflowLayout from '../OnBase/OnBaseWorkflowLayout'


const OnBaseLayout = ({updateOptions, selectedOption}) => {
    const [activeLayout, setActiveLayout] = useState('Workflow')


    const updateActiveLayout = (layout) => {
        console.log('setting active layout..')
        setActiveLayout(() => layout)
    }

    const onLayoutButtonClick = (e) => {
        
        console.log('e.target.value: ', e.target.value)
        setActiveLayout(e.target.value)

    }

    return (
        <>

        
        {/* return the options to app to update the state */}
        {activeLayout === 'Workflow' ? updateOptions(OnBaseWorkflowOptions) : updateOptions(null)}
        {}
        
        
        <div className='mxnet-layout-container'>
            <div className='layout-options-container'>
                <button value={'Workflow'} onClick={e => onLayoutButtonClick(e)}>Workflow</button>
                
                   
            </div>
            {activeLayout === 'Workflow' ? <OnBaseWorkflowLayout selectedOption={selectedOption} /> : <OnBaseWorkflowLayout />}
            {/* <MainLayout /> */}
            {/* <RtsDetailsLayout /> */}


        </div>

        </>
    )
}

export default OnBaseLayout
